import React, { Component, useState,useEffect} from 'react'
import {Switch, Route, withRouter} from 'react-router-dom'
import logo from './logo.svg';
import 'bootstrap/dist/css/bootstrap.css'
import './App.css';
import axios from 'axios';
import {API_URL} from './config'

//#region Components

import SignIn from './components/auth/SignIn';
import LaundryHome from './components/laundry/LaundryHome';
import HotelHome from './components/hotel/HotelHome';
import Customers from './components/laundry/CustomersList';
import Users from './components/laundry/Users';
import Laundries from './components/laundry/Laundries';
import ComplexLaundries from './components/laundry/ComplexLaundries';
import EditService from './components/hotel/EditService';
import Footer from './components/main/Footer';

//#endregion Components


import { ThemeConsumer } from 'react-bootstrap/esm/ThemeProvider';

function App() {

  const [loggedInUser, setLoggednUser] = useState(null);
  const [err, setError] = useState(false);
  const [errorMessage, setErrorMessage] = useState(null);

  useEffect(() => {
    if(!loggedInUser){
      axios.get(`${API_URL}/user`, {withCredentials: true})
        .then((result) => {
          setLoggednUser(result.data)
        })
    }
  }, [])

  const handleSignIn = (e) =>{
    e.preventDefault();
    const {email, password} = e.currentTarget;
    axios.post(`${API_URL}/signin`,{email: email.value, password: password.value},  {withCredentials: true})
      .then((res)=>{
        setLoggednUser(res.data)
        window.location.reload(false);
      })
      .catch((err) => {
        setError(true);
        let error = err.response.data.error;
        console.log(error)
        setErrorMessage(error);
      })
  }

  const handleLogOut = () =>{
    axios.post(`${API_URL}/logout`, {}, {withCredentials: true})
    .then(()=>{
      setLoggednUser(null);
      window.location.reload(false);
    })
  }

  if(err && errorMessage==null){ return(<p>Loading...</p>)}
    return (
      <>
      <Switch>
        <Route exact path="/" render ={()=>{
          return <SignIn 
                  onSignIn = {handleSignIn} 
                  loggedInUser={loggedInUser}
                  err= {err}
                  errorMessage={errorMessage}
                  />
        }}/>
        <Route path="/laundry/home" render ={()=>{
          return <LaundryHome loggedInUser={loggedInUser} logOut = {handleLogOut}/>
        }}/>
        <Route path="/hotel/home" render ={()=>{ 
          return <HotelHome loggedInUser={loggedInUser} logOut = {handleLogOut}/>
        }}/>
        <Route path="/laundry/complexes" render ={()=>{ 
          return <Customers loggedInUser={loggedInUser} logOut = {handleLogOut}/>
        }}/>
        <Route path="/laundry/users" render ={()=>{ 
          return <Users loggedInUser={loggedInUser} logOut = {handleLogOut}/>
        }}/>
        <Route path="/laundry/laundries" render ={()=>{ 
          return <Laundries loggedInUser={loggedInUser} logOut = {handleLogOut}/>
        }}/>
        <Route path="/complex/:id/laundries" render={(routeProps) => {
          return <ComplexLaundries 
                    {...routeProps} 
                    loggedInUser={loggedInUser}
                    logOut = {handleLogOut}
                  />
        }}/>
        <Route path="/customer/service/:id/edit" render ={(routeProps)=>{ 
          return <EditService 
                {...routeProps} 
                loggedInUser={loggedInUser} 
                logOut = {handleLogOut}
                />
        }}/>
      </Switch>
      <Footer/>
      </>
    )
  }
export default withRouter(App)